@import url("https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap");

@font-face {
	font-display: swap;
	font-family: "Mazzard H";
	src: url("./assets/fonts/MazzardH-SemiBold.ttf");
}

@font-face {
	font-display: swap;
	font-family: "Mazzard H Medium";
	src: url("./assets/fonts/MazzardH-Medium.ttf");
}

@font-face {
	font-display: swap;
	font-family: "Mazzard H Bold";
	src: url("./assets/fonts/MazzardH-Bold.ttf");
}

:root {
	--gray-100: #1e1e1e;
	--gray-300: #252526;
	--gray-500: #333333;
	--gray-700: #999999;
	--gray-900: #e1e1e1;

	--primary-100: #53002e;
	--primary-300: #8a014c;
	--primary-500: #e6007e;
	--primary-700: #ff69bc;

	--secondary: #ff7c00;

	--label-new: #40e0d0;
	--label-new-border: #abfff7;

	--label-popular: #ebb787;
	--label-popular-border: #ffe6ce;
	
	--label-discount: #ffff00;
	--label-discount-border: #ffff71;
}

html {
	scroll-behavior: smooth;
}

body {
	margin: 0;
	padding: 0 17.5%;
	font-family: "Roboto";

	background: url("./assets/pattern.png") repeat-x, var(--gray-100);
	animation: bg-loop infinite 10s linear;
	color: var(--gray-900);
}

h1, h2, h3, h4, h5, h6 {
	font-family: "Mazzard H";
	margin: 0;
}

h1, h2 {
	font-family: "Mazzard H Bold";
}

h1 { font-size: 24px }
h2 { font-size: 21px }
h3 { font-size: 18px }
h4 { font-size: 16px }
h5 { font-size: 14px }
h6 { font-size: 12px }

@keyframes bg-loop {
	0% {
		background-position: 0 0;
	}

	100% {
		background-position: 196px 0;
	}
}